.Project-Panel {
  z-index: 1;
  position: absolute;
  top: 0;
  left: 0;
  margin: 0.5rem;

  border-radius: 0.4em;
  border-style: solid;
  border-width: 0.2em;
  color: #A9A9A9;
  background: white;

  text-align: center;
  align-items: center;
  justify-content: center;

  img {
    width: 2em;
    height: 2em;
    margin: 0.5em;
  }

  p {
    font-size: 0.8em;
    color: black;
    margin: 0 0.5em 0.5em 0.5em;
  }

  p:visited {
    color: #0000FFA0;
  }

  &:hover {
    background-color: #A9A9A9;
  }

  @media (max-width: 425px) {
    //position: relative;
    //left: 50%;
    //right: 50%;
    //transform: translateY(-4.0rem);
    display: none;
    //transition: transform 0.5s ease;
  }

  //&:hover {
  //  transform: translateY(0);
  //  transition: transform 0.5s ease;
  //}
}
